import Vue from "vue";
import VueRouter from "vue-router";
import Test from "../components/Test.vue";
import Main from "../components/Main.vue";
import Ex from "../components/Ex.vue";
Vue.use(VueRouter);


const routes = [{
    path: "/",
    name: "Main",
    component: Main
  },
  {
    path: "/test",
    name: "Test",
    component: Test
  },
  {
    path: "/ex",
    name: "Ex",
    component: Ex
  }
];

const router = new VueRouter({
  routes
});

export default router;