            class: true
          },
          answers: [
            {
              title: "Да, чат нужен для быстрого получения важной информации",
              value: 2
            },
            {
              title: "Нет, вполне можно ограничиться личным общением",
              value: 0
            }
          ]
        },
        {
          question: {
            title: "Используете ли Вы голосовые сообщения?",
            class: true
          },
          answers: [
            { title: "Да", value: 0 },
            { title: "Нет", value: 2 },
            {
              title: "Иногда",
              value: 1
            }
          ]
        },
        {
          question: {
            title: "Как Вы обращаетесь к педагогу?",
            class: true
          },
          answers: [
            {
              title: "Обращаюсь к педагогу на Вы, называю по имени отчеству",
              value: 2
            },
            { title: "Обращаюсь как к другу", value: 0 }
          ]
        }
      ]
    };
  },
  methods: {
    begin() {
      this.$router.push("/");
    },
    next() {
      console.log("this.countert", this.counter);
      console.log("this.questions.length", this.questions.length);

      this.total += +this.selected;
      this.selected = null;
      if (this.counter + 2 < this.questions.length) {
        this.questions[this.counter].question.class = true;
        this.questions[this.counter + 1].question.class = false;
      } else if (this.counter + 2 == this.questions.length) {
        this.questions[this.counter].question.class = true;
        this.result = false;
        this.name = this.$store.getters.USERNAME;

        if (this.total >= 19) {
          this.testResult = this.name + ", Вы отличный собеседник";
        } else if (this.total < 19 && this.total >= 7) {
          this.testResult =
            this.name +
            ", Вы хороший собеседник, но вам стоит исправить некоторые привычки";
        } else {
          this.testResult =
            this.name +
            ", Вы не очень хороший собеседник. Вам стоит пересмотреть свой взгляд на общение в мессенджерах";
        }
      }
      this.counter++;
    }
  }
};
