import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        userName: null,
    },
    getters: {
        USERNAME: state => {
            return state.userName;
        },

    },
    mutations: {
        writeUserName(state, name) {
            state.userName = name;
        },
    },
});