<template>
  <div class="main">
    <b-container>
      <div class="welcome-card-wrapper">
        <div class="welcome-card">
          <h1 class="title">Как вы общаетесь в мессенджерах?</h1>
          <p>21 век - это эпоха информационных технологий. Люди стали чаще общаться в социальных сетях, в мессенджерах.</p>
