<script>
export default {
  name: "Main",
  data() {
    return {
      name
    };
  },
  computed: {
    showAlert() {
      return this.name.length > 2 ? true : false;
    }
  },
  methods: {
    saveName() {
      this.$store.commit("writeUserName", this.name);
    }
  }
};
</script>

